.voice-interaction-page {
  text-align: center;
  padding: 40px;
  background: linear-gradient(to right, #f8f9fa, #e9ecef);
  min-height: 100vh;
}

.transcript-text {
  font-size: 1.2rem;
  margin-top: 20px;
}

.speech-box {
  background: white;
  margin: 20px auto;
  padding: 20px;
  border-radius: 12px;
  box-shadow: 0 4px 10px rgba(0,0,0,0.1);
  max-width: 500px;
  font-size: 1.1rem;
  font-weight: 500;
}

.back-btn {
  display: inline-block;
  margin-top: 20px;
  padding: 10px 20px;
  background: #6c63ff;
  color: white;
  border-radius: 8px;
  text-decoration: none;
  transition: background 0.3s ease;
}
.back-btn:hover {
  background: #5a52d4;
}
